"use client"

import { ExternalLink, ArrowUpRight, ArrowDownRight, Code, Clock, Loader2 } from "lucide-react"
import { useWalletTransactions } from "@/hooks/useWalletTransactions"

interface TransactionsViewProps {
  walletAddress: string
}

// Helper function to format wei to USDC
function formatWeiToEth(wei: string): string {
  try {
    const weiBigInt = BigInt(wei || "0")
    const usdc = Number(weiBigInt) / 1e18
    return usdc.toFixed(6)
  } catch {
    return "0.000000"
  }
}

export function TransactionsView({ walletAddress }: TransactionsViewProps) {
  const { transactions, loading, error } = useWalletTransactions(walletAddress, 50)

  if (loading) {
    return (
      <div className="bg-card/50 backdrop-blur-sm border border-border rounded-2xl overflow-hidden">
        <div className="p-7 bg-gradient-to-r from-primary/5 to-accent/5 border-b border-border">
          <div className="flex items-center gap-3 mb-2">
            <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
              <Clock className="h-5 w-5 text-primary" />
            </div>
            <h3 className="text-xl font-bold text-foreground">Recent Transactions</h3>
          </div>
          <p className="text-sm text-muted-foreground ml-13">Your latest on-chain activity on Arc Network</p>
        </div>
        <div className="p-16 text-center">
          <Loader2 className="h-8 w-8 animate-spin text-primary mx-auto mb-4" />
          <p className="text-muted-foreground">Loading transactions...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-card/50 backdrop-blur-sm border border-border rounded-2xl overflow-hidden">
        <div className="p-7 bg-gradient-to-r from-primary/5 to-accent/5 border-b border-border">
          <div className="flex items-center gap-3 mb-2">
            <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
              <Clock className="h-5 w-5 text-primary" />
            </div>
            <h3 className="text-xl font-bold text-foreground">Recent Transactions</h3>
          </div>
          <p className="text-sm text-muted-foreground ml-13">Your latest on-chain activity on Arc Network</p>
        </div>
        <div className="p-16 text-center">
          <p className="text-destructive mb-2">Error loading transactions</p>
          <p className="text-sm text-muted-foreground">{error}</p>
        </div>
      </div>
    )
  }

  if (transactions.length === 0) {
    return (
      <div className="bg-card/50 backdrop-blur-sm border border-border rounded-2xl overflow-hidden">
        <div className="p-7 bg-gradient-to-r from-primary/5 to-accent/5 border-b border-border">
          <div className="flex items-center gap-3 mb-2">
            <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
              <Clock className="h-5 w-5 text-primary" />
            </div>
            <h3 className="text-xl font-bold text-foreground">Recent Transactions</h3>
          </div>
          <p className="text-sm text-muted-foreground ml-13">Your latest on-chain activity on Arc Network</p>
        </div>
        <div className="p-16 text-center">
          <p className="text-muted-foreground">No transactions found for this wallet</p>
        </div>
      </div>
    )
  }

  const getTransactionType = (tx: typeof transactions[0]) => {
    if (tx.isContractCreation) return "contract"
    if (tx.to && tx.to.toLowerCase() === walletAddress.toLowerCase()) return "receive"
    return "send"
  }

  const getTypeIcon = (tx: typeof transactions[0]) => {
    const type = getTransactionType(tx)
    switch (type) {
      case "send":
        return <ArrowUpRight className="h-5 w-5 text-destructive" />
      case "receive":
        return <ArrowDownRight className="h-5 w-5 text-success" />
      case "contract":
        return <Code className="h-5 w-5 text-primary" />
      default:
        return <Code className="h-5 w-5 text-primary" />
    }
  }

  const getTypeLabel = (tx: typeof transactions[0]) => {
    const type = getTransactionType(tx)
    switch (type) {
      case "send":
        return "Sent"
      case "receive":
        return "Received"
      case "contract":
        return tx.isContractCreation ? "Contract Creation" : "Contract Call"
      default:
        return "Transaction"
    }
  }

  const formatHash = (hash: string) => {
    return `${hash.slice(0, 10)}...${hash.slice(-8)}`
  }

  return (
    <div className="bg-card/50 backdrop-blur-sm border border-border rounded-2xl overflow-hidden">
      <div className="p-7 bg-gradient-to-r from-primary/5 to-accent/5 border-b border-border">
        <div className="flex items-center gap-3 mb-2">
          <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
            <Clock className="h-5 w-5 text-primary" />
          </div>
          <h3 className="text-xl font-bold text-foreground">Recent Transactions</h3>
        </div>
        <p className="text-sm text-muted-foreground ml-13">Your latest on-chain activity on Arc Network</p>
      </div>

      <div className="divide-y divide-border">
        {transactions.map((tx) => {
          const amount = formatWeiToEth(tx.value)
          const timestamp = new Date(tx.timestamp).toLocaleString()
          const isSuccess = tx.status === 1

          return (
            <div
              key={tx.hash}
              className="p-6 hover:bg-primary/5 transition-all duration-200 flex items-center justify-between group"
            >
              <div className="flex items-center gap-5">
                <div className="h-12 w-12 rounded-xl bg-gradient-to-br from-muted to-muted/50 flex items-center justify-center group-hover:from-primary/20 group-hover:to-accent/20 transition-all">
                  {getTypeIcon(tx)}
                </div>
                <div>
                  <div className="flex items-center gap-2">
                    <p className="font-semibold text-foreground text-base mb-1">{getTypeLabel(tx)}</p>
                    {!isSuccess && (
                      <span className="text-xs bg-destructive/10 text-destructive px-2 py-0.5 rounded">Failed</span>
                    )}
                  </div>
                  <p className="text-sm text-muted-foreground font-mono">{formatHash(tx.hash)}</p>
                  <p className="text-xs text-muted-foreground mt-1">Block #{tx.blockNumber}</p>
                </div>
              </div>

              <div className="flex items-center gap-6">
                <div className="text-right">
                  <p className="font-bold text-foreground text-base mb-1">{amount} USDC</p>
                  <p className="text-sm text-muted-foreground">{timestamp}</p>
                </div>
                <a
                  href={`https://testnet.arc.network/tx/${tx.hash}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="h-10 w-10 rounded-xl hover:bg-primary/10 flex items-center justify-center transition-colors group-hover:border group-hover:border-primary/30"
                >
                  <ExternalLink className="h-4 w-4 text-muted-foreground group-hover:text-primary" />
                </a>
              </div>
            </div>
          )
        })}
      </div>

      {transactions.length > 0 && (
        <div className="p-7 border-t border-border text-center bg-muted/20">
          <p className="text-sm text-muted-foreground">
            Showing {transactions.length} recent transactions
          </p>
        </div>
      )}
    </div>
  )
}
