"use client"

import { useMemo, useState } from "react"
import { WalletOverview } from "@/lib/api"
import { Activity, Zap, Wallet, Code } from "lucide-react"

type ArcScoreHeroProps = {
  data: WalletOverview
}

const TIERS = [
  { name: "Bronze", min: 0, max: 200, color: "text-amber-500", badge: "border-amber-500/40 bg-amber-500/10" },
  { name: "Prata", min: 200, max: 500, color: "text-slate-200", badge: "border-slate-300/50 bg-slate-200/10" },
  { name: "Ouro", min: 500, max: Infinity, color: "text-amber-300", badge: "border-amber-300/50 bg-amber-300/10" },
]

export function ArcScoreHero({ data }: ArcScoreHeroProps) {
  const [showTiers, setShowTiers] = useState(false)

  const { tier, nextTier, pointsToNext, progressPercent, reputationText } = useMemo(() => {
    const score = data.score ?? 0
    const tier = TIERS.find((t) => score >= t.min && score < t.max) ?? TIERS[TIERS.length - 1]
    const nextTierIndex = TIERS.indexOf(tier) + 1
    const nextTier = TIERS[nextTierIndex] ?? null
    const span = (nextTier ? nextTier.min : tier.max) - tier.min || 1
    const progressWithinTier = Math.min(Math.max((score - tier.min) / span, 0), 1)
    const pointsToNext = nextTier ? Math.max(nextTier.min - score, 0) : 0

    const reputationText =
      data.status === "Excellent"
        ? "Excellent reputation"
        : data.status === "Good"
          ? "Good reputation"
          : "Needs improvement"

    return {
      tier,
      nextTier,
      pointsToNext,
      progressPercent: Math.round(progressWithinTier * 100),
      reputationText,
    }
  }, [data.score, data.status])

  const factors = [
    { label: "Transactions", value: data.totalTransactions.toLocaleString(), icon: Activity },
    { label: "Active days", value: data.activeDays.toLocaleString(), icon: Zap },
    { label: "Contract interactions", value: data.contractInteractions?.toLocaleString() ?? "0", icon: Code },
    {
      label: "Wallet balance",
      value: `${(Number(data.balance.native || 0) / 1e18).toFixed(4)} native`,
      icon: Wallet,
    },
  ]

  return (
    <section className="relative overflow-hidden rounded-3xl border border-border bg-gradient-to-br from-[#1a5cff26] via-[#6a5acd33] to-[#8a4fff26] p-6 md:p-8 shadow-xl">
      <div className="absolute inset-0 pointer-events-none bg-[radial-gradient(circle_at_30%_20%,rgba(26,92,255,0.16),transparent_35%),radial-gradient(circle_at_80%_0%,rgba(138,79,255,0.16),transparent_25%)]" />

      <div className="relative flex flex-col lg:flex-row lg:items-start lg:justify-between gap-6">
        <div className="space-y-4 lg:max-w-2xl">
          <div className="text-6xl md:text-7xl font-black tracking-tight text-foreground leading-none -mt-4">
            Score
          </div>

        <div className="flex items-end gap-4">
          <div className="text-6xl md:text-7xl font-black tracking-tight text-foreground drop-shadow-sm">
            {Math.round(data.score)}
          </div>
          <div className="space-y-2 relative">
            <button
              type="button"
              onClick={() => setShowTiers((p) => !p)}
              className={`inline-flex items-center gap-2 rounded-full px-4 py-2 text-sm font-semibold border ${tier.badge} ${tier.color} transition-all hover:shadow-md hover:bg-amber-400 hover:text-amber-950`}
            >
              TIER
            </button>
            {showTiers && (
              <div className="absolute z-10 mt-2 w-44 rounded-xl border border-border/70 bg-card/90 backdrop-blur-sm shadow-xl p-2 space-y-2">
                {TIERS.map((t) => (
                  <button
                    key={t.name}
                    type="button"
                    onClick={() => setShowTiers(false)}
                    className={`w-full text-left text-sm font-semibold px-3 py-2 rounded-lg border transition-colors ${
                      t.name === tier.name
                        ? `${t.badge} ${t.color}`
                        : "border-transparent hover:border-border/60 hover:bg-muted/40 text-foreground"
                    }`}
                  >
                    {t.name}
                  </button>
                ))}
              </div>
            )}
            <div className="text-muted-foreground text-sm">{reputationText}</div>
            <div className="text-xs text-muted-foreground">
              {nextTier ? `Only ${pointsToNext} points to reach ${nextTier.name}` : "Max tier reached"}
            </div>
          </div>
        </div>

          <div className="space-y-3">
            <div className="flex items-center justify-between text-sm font-medium text-muted-foreground">
              <span>Progress {tier.name}</span>
              <span className="text-foreground font-semibold">
                {Math.round(data.score)} {nextTier ? `/ ${nextTier.min}` : ""}
              </span>
            </div>
            <div className="h-4 md:h-5 w-full rounded-full bg-white/20 border border-white/25 overflow-hidden shadow-[0_0_0_1px_rgba(255,255,255,0.12)]">
              <div
                className="h-full rounded-full transition-[width] duration-700 ease-out shadow-[0_0_14px_rgba(138,79,255,0.55)] bg-[linear-gradient(120deg,rgba(26,92,255,0.95),rgba(251,191,36,0.95),rgba(138,79,255,0.95))] bg-[length:240%_100%] animate-[shimmer_3.2s_ease-in-out_infinite]"
                style={{ width: `${progressPercent}%` }}
              />
            </div>
          </div>
        </div>

        <div className="lg:w-96 rounded-2xl border border-border/60 bg-card/70 backdrop-blur-sm p-5 shadow-lg">
          <div className="text-lg font-semibold text-foreground mb-2">Your On-Chain Reputation</div>
          <p className="text-sm text-foreground/85">
            Your ARC Score represents your on-chain behavior across the Arc Network.
          </p>
          <p className="text-sm text-foreground/75 mt-2">
            The more you interact and stay active, the stronger your reputation becomes.
          </p>
        </div>
      </div>
    </section>
  )
}

