const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api/v1';

export interface WalletOverview {
  totalTransactions: number;
  walletAge: number;
  activeDays: number;
  contractInteractions: number;
  balance: {
    native: string;
    usdc: string;
  };
  score: number;
  rank: number | null;
  percentile: number | null;
  status: string;
  firstSeenAt: string;
  lastSeenAt: string;
}

export interface Transaction {
  hash: string;
  blockNumber: string;
  from: string;
  to: string | null;
  value: string;
  gasUsed: string;
  gasPrice: string;
  timestamp: string;
  status: number;
  isContractCreation: boolean;
  contractAddress: string | null;
}

export interface NetworkStats {
  totalWallets: number;
  totalTransactions: string;
  totalActiveWallets: number;
  avgTransactionsPerWallet: number;
  medianTransactionsPerWallet: number;
  lastProcessedBlock: string;
  lastUpdated: string;
}

export interface ApiResponse<T> {
  success: boolean;
  data?: T;
  error?: string;
}

async function fetchApi<T>(endpoint: string): Promise<T> {
  const response = await fetch(`${API_BASE_URL}${endpoint}`);
  
  if (!response.ok) {
    throw new Error(`API Error: ${response.status} ${response.statusText}`);
  }
  
  const result: ApiResponse<T> = await response.json();
  
  if (!result.success) {
    throw new Error(result.error || 'API request failed');
  }
  
  if (!result.data) {
    throw new Error('No data returned from API');
  }
  
  return result.data;
}

export const api = {
  getWalletOverview: (address: string): Promise<WalletOverview> => {
    return fetchApi<WalletOverview>(`/wallet/${address}/overview`);
  },
  
  getWalletTransactions: (address: string, limit = 100, offset = 0): Promise<{
    transactions: Transaction[];
    pagination: {
      total: number;
      limit: number;
      offset: number;
      hasMore: boolean;
    };
  }> => {
    return fetchApi(`/wallet/${address}/transactions?limit=${limit}&offset=${offset}`);
  },
  
  getNetworkStats: (): Promise<NetworkStats> => {
    return fetchApi<NetworkStats>('/network/stats');
  },
  
  getLeaderboard: (limit = 100, offset = 0): Promise<{
    leaderboard: Array<{
      rank: number;
      wallet: string;
      score: number;
      percentile: number | null;
      totalTransactions: number;
      activeDays: number;
      status: string;
    }>;
    pagination: {
      total: number;
      limit: number;
      offset: number;
      hasMore: boolean;
    };
  }> => {
    return fetchApi(`/network/leaderboard?limit=${limit}&offset=${offset}`);
  },
};



