"use client"

import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts"
import { TrendingUp, Loader2 } from "lucide-react"
import { useWalletTransactions } from "@/hooks/useWalletTransactions"
import { useMemo } from "react"

interface ActivityChartsProps {
  walletAddress: string
}

// Helper function to format wei to USDC
function formatWeiToEth(wei: string): number {
  try {
    const weiBigInt = BigInt(wei || "0")
    return Number(weiBigInt) / 1e18
  } catch {
    return 0
  }
}

export function ActivityCharts({ walletAddress }: ActivityChartsProps) {
  const { transactions, loading, error } = useWalletTransactions(walletAddress, 1000) // Get more transactions for chart

  // Group transactions by week
  const weeklyData = useMemo(() => {
    if (!transactions || transactions.length === 0) {
      return []
    }

    // Get last 12 weeks
    const weeks: { [key: string]: { transactions: number; volume: number; contracts: Set<string> } } = {}
    const now = new Date()
    
    for (let i = 11; i >= 0; i--) {
      const weekStart = new Date(now)
      weekStart.setDate(weekStart.getDate() - (i * 7))
      weekStart.setHours(0, 0, 0, 0)
      const weekKey = `W${12 - i}`
      weeks[weekKey] = {
        transactions: 0,
        volume: 0,
        contracts: new Set(),
      }
    }

    // Process transactions
    transactions.forEach((tx) => {
      const txDate = new Date(tx.timestamp)
      const weekNumber = Math.floor((now.getTime() - txDate.getTime()) / (7 * 24 * 60 * 60 * 1000))
      
      if (weekNumber >= 0 && weekNumber < 12) {
        const weekKey = `W${12 - weekNumber}`
        if (weeks[weekKey]) {
          weeks[weekKey].transactions++
          weeks[weekKey].volume += formatWeiToEth(tx.value)
          if (tx.to && tx.to !== walletAddress.toLowerCase()) {
            weeks[weekKey].contracts.add(tx.to)
          }
        }
      }
    })

    // Convert to array format
    return Object.entries(weeks).map(([week, data]) => ({
      week,
      transactions: data.transactions,
      volume: data.volume.toFixed(2),
      contracts: data.contracts.size,
    }))
  }, [transactions, walletAddress])

  const CustomTooltip = ({ active, payload }: any) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload
      return (
        <div className="bg-card/95 backdrop-blur-sm border border-border rounded-xl p-4 shadow-2xl">
          <p className="font-bold text-foreground mb-3 text-base">{data.week}</p>
          <div className="space-y-2 text-sm">
            <div className="flex items-center justify-between gap-8">
              <span className="text-muted-foreground">Transactions:</span>
              <span className="text-foreground font-semibold">{data.transactions}</span>
            </div>
            <div className="flex items-center justify-between gap-8">
              <span className="text-muted-foreground">Volume:</span>
              <span className="text-foreground font-semibold">{data.volume} USDC</span>
            </div>
            <div className="flex items-center justify-between gap-8">
              <span className="text-muted-foreground">Contracts:</span>
              <span className="text-foreground font-semibold">{data.contracts}</span>
            </div>
          </div>
        </div>
      )
    }
    return null
  }

  return (
    <div className="bg-card/50 backdrop-blur-sm border border-border rounded-2xl overflow-hidden">
      <div className="p-7 bg-gradient-to-r from-primary/5 to-accent/5 border-b border-border">
        <div className="flex items-center gap-3 mb-2">
          <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
            <TrendingUp className="h-5 w-5 text-primary" />
          </div>
          <h3 className="text-xl font-bold text-foreground">Weekly Transactions</h3>
        </div>
        <p className="text-sm text-muted-foreground ml-13">Transaction activity across the last 12 weeks</p>
      </div>

      <div className="p-7">
        <ResponsiveContainer width="100%" height={280}>
          <BarChart data={weeklyData}>
            <CartesianGrid strokeDasharray="3 3" stroke="hsl(var(--border))" opacity={0.3} />
            <XAxis
              dataKey="week"
              stroke="hsl(var(--muted-foreground))"
              fontSize={13}
              tickLine={false}
              axisLine={false}
            />
            <YAxis stroke="hsl(var(--muted-foreground))" fontSize={13} tickLine={false} axisLine={false} />
            <Tooltip content={<CustomTooltip />} cursor={{ fill: "hsl(var(--primary))", opacity: 0.1 }} />
            <Bar dataKey="transactions" fill="hsl(var(--primary))" radius={[10, 10, 0, 0]} maxBarSize={60} />
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  )
}
